

body {
    font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica",
    "Arial", sans-serif;
    background-color: #ffffff;
    font-weight: 100;
    overflow-x:hidden;
  
}
.root {
    overflow:hidden;
}

h1, p {
    color: black;
}

h1 {
    font-weight: 200;
}
.coin-container {
  
    min-width: 1150px;
    display: flex;
    justify-content: center;
    padding-bottom: 50px;

}

input {
    border-radius: 1%;
    width: 200px;
    background: rgba( 255, 255, 255, .10 );
    box-shadow: 0 0px 0px 1px #E0E0E0;
    border: 1px solid white;
    box-shadow: 0px 0px 10px 1px #E0E0E0;
    height: 20px;
    border-radius: 50px;
    text-align: center;
    
}



.coinquery {
    
    padding: 50px;
    padding-bottom: 40px;
    justify-content: center;
    text-align: center;
    text-align: center;
}


.coin-row, .cat {
    overflow:hidden;
    border-radius: 50px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
 min-width: 1100px;
    justify-content: space-between;
    background: rgba( 255, 255, 255, .10 );
    border: 1px solid white;
    box-shadow: 0px 0px 15px 1px #E0E0E0;
  
}


.catCont {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    min-width: 1150px;
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
}
.cat {
    height: 40px;
    margin-top: 60px;
  
   
  
}



.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
  
    min-width: 300px;
}
.coin h1 {
    font-size: 20px;
    padding-left: 20px;
}
.coin img {
    width: 50px;
    padding-left: 20px;
}

.coin-symbol {
    display: flex;
    padding: 20px;
}
.coin-data, .catdata {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
   margin: 50px
}
.catdata {
    width: 69%;
    margin: 65px
}

.cat p:nth-child(1) {
    margin-left: 100px;
}


.coin-symbol {
    text-transform: uppercase;
}

.coin-percent.red {
color: rgba(255,69,58);
}
.coin-percent.green {
    color: rgba(48,209,88);
    }

    .imgs {
        width: 25px;
    }

    .info {
        padding-top: 20px;
    }

    .coin-container {
animation: fade 2s linear forwards;
    }


@keyframes fade {
    from {
        visibility: hidden;
        opacity: 0;
        transform: translateY(0px);
      }
      to {
        visibility: visible;
        opacity: 1;
        transform: translateY(10.5px);
      }
  }


  



  @media (max-width: 1200px) {
    .coin-row, .cat {
       
    
        min-width:  430px;
       
      
    }
    .coin-data,.catdata {
        overflow-y:hidden;
        flex-grow: 1;
        margin: 40px;
        gap:60px;
    }

    .catdata { 
      
        white-space:nowrap;
        gap:60px;
        
    }
    
   
   
    

     .coin-data, .catdata{
        width: 680px;
     }

     .catdata {
        width: 840px;
     }

     
     .cat p:nth-child(5) {
        margin-right: 20px;
    }
     

.coin-data {
    margin: 45px;
}
   
    .coin-container, .catCont  {
        min-width:  450px;

        display: flex;
        justify-content: center;
        padding-bottom: 50px;
    
    }
  
   

   
}

@media (max-width: 500px) {
   

   
    .coin-container, .catCont  {
        min-width:  350px;
    
    }

    .coin-data {
        margin: 0px;
    }
   
    .cat p:nth-child(1) {
        margin-right: 10px;
    }

    .coin-data {
       min-width: 0px;
       padding-right: 40px;
       
    }
    
    .coin-row, .cat {
       
    min-width: 400px;
        width: 440px;
    }





    .catdata{
        padding-left: 50px;
    
        gap: 10;
    }
}